import React from 'react';
import Ban from './Ban';
import ban_data from './data/banner';
import Logoletter from './module/Logoletter';


  //    
function MakeList(props) {
  return (
    <li data-seq={props.idx}>
      <div>
      <img className='banimg' src={props.rec['src']} alt='' />
      </div>
      <div className='hidehouse'>
   
      <h3 className="tithead roof"><span className='spanhead'>{props.tit['tit1'].split('^')[0]}</span> 
<span>{props.tit['tit1'].split('^')[1]}</span> </h3>

      <div className='hide first '>
      </div>
  

      <h3 className="tithead middle"><span className='spanhead'>{props.tit['tit2'].split('^')[0]}</span> 
<span>{props.tit['tit2'].split('^')[1]}</span>  </h3>
      <div className='hide second'>
      </div>


      <h3 className="tithead bottom"><span className='spanhead'>{props.tit['tit3'].split('^')[0]}</span> 
<span>{props.tit['tit3'].split('^')[1]}</span> </h3>
      {/* <div className='hide third'>
      </div> */}
    

      </div>

    </li>
  );
} ///////MakeList////////////////


const Company = (props) => {
  const sel_data = ban_data.COMPANY;

  return (
    <>
    <div id='company' className="company" >
      <section className='inbx headline'>
        <h2>company</h2>
        <div className='banner'>
          {/*  */}
          <ul className='slider'>
            {sel_data.map((x, i) => (
              <MakeList rec={x} tit={x} key={i} idx={i} />
            ))
            }
          </ul>
         
        
          
          {/*JS/jquery !!   */}
          {/* {jqFn()} */}
        </div>
      </section>
    </div>

<Logoletter cat='COMPANY' mm='on' />
</>
  );
} ///////Company///////////

 

export default Company;
