// Temp.js

import $ from 'jquery';
import '../css/logoletter.css';
import logoletter_data from '../data/logoletter';

//   

// {'slide slide-four' +' '+props.mm}

function jqFn() {
  $(() => {});
} ///////jqFn/////

function Logoletter(props) {
  const logo_data = logoletter_data[props.cat];
  return (
    <>
      <div id='third' className={'slide slide-three' + ' ' + props.mm}>
        {/*  */}
        {/*   JS    */}

        <section className='logoletter inbx'>
          {logo_data.map((x, i) => (
            <article key={i}>
              <h5 className='logo'>{x.logo}</h5>
            </article>
          ))}
        </section>

      </div>
    </>
  );
}

export default Logoletter;
