// Temp.js

import $ from 'jquery';
import '../css/mainletter.css';
import mainletter_data from '../data/mainletter';

//   

function jqFn() {
  $(() => {});
} ///////jqFn/////

function Mainletter() {
  return (
    <>
      <div id='second' className='slide slide-two'>
        <section className='letterbx inbx'>
          {mainletter_data.map((x, i) => (
            <article key={i}>
              <h3>{x.tit.split('^')[0]} </h3>
              <h3 className='mark'>{x.tit.split('^')[1]}</h3>

              <h4 className='mainletter'>{x.exp.split('^')[0]} </h4>
              <h4 className='mainletter yellow'>{x.exp.split('^')[1]} </h4>
            </article>
          ))}
        </section>
        {/*  */}
        {/*   JS    */}

        {jqFn()}
      </div>
    </>
  );
}

export default Mainletter;
