// Temp.js

import $ from 'jquery';
import '../css/workswipe.css';
import firstletter_data from '../data/firstletter';

function News(props) {
   const img = firstletter_data;
  return (
    <div id='seventh' className={'slide slide-seven' + ' ' + props.mm}>
      <section className='inbx'>
        <div className='ournews'>
          <article className='newstitle '>
            <div className='newsname'>
              <h3 className='h3tit'>Service</h3>

              <div id='containerbx'>
                <h2 className='subtit'>MAKE</h2>
                <div id='flipbx'>
                  <div className='flipbg first'>
                    <h2 className='fliptit '>CONTENTS</h2>
                  </div>
                  <div className='flipbg second'>
                    <h2 className='fliptit'>INFLUENCER</h2>
                  </div>
                  <div className='flipbg third'>
                    <h2 className='fliptit'>MARKETING</h2>
                  </div>
                </div>
                <h2 className='subtit'>BETTER</h2>
              </div>
            </div>
          </article>
          <div>
              <img className='arrow' src='./image/svg/arrow.svg' alt='' />
            </div>
          <article className='picture'>
            {/* <div>
              <img className='arrow' src='./image/svg/arrow.svg' alt='' />
            </div> */}
            <div>
              <img className='phone' src={img.phone} alt='' />
            </div>
            {/* <div className='talkdiv'>
              <img className='talk' src={img.talk} alt='' />
            </div> */}
          </article>
        </div>
      </section>
    </div>
  );
}

export default News;
