import React from 'react';
import ban_data from './data/banner';
import WorkSwipe from './module/WorkSwipe';

//    
function MakeList(props) {
  return (
    <li data-seq={props.idx}>
      <div>
        <img className='banimg' src={props.rec['src']} alt='' />
      </div>
      <div className='hidehouse'>
        <h3 className='tithead roof work'>
          <span className='spanhead'>{props.tit['tit1'].split('^')[0]}</span>
          <span>{props.tit['tit1'].split('^')[1]}</span>
        </h3>

        <div className='hide first work'></div>
      </div>
    </li>
  );
} ///////MakeList////////////////

const Work = (props) => {
  const sel_data = ban_data.WORK;

  return (
    <>
      <div id='work' className='work'>
        <section className='inbx headline'>
          <h2>work</h2>
          <div className='banner'>
            {/*  */}
            <ul className='slider'>
              {sel_data.map((x, i) => (
                <MakeList rec={x} tit={x} key={i} idx={i} />
              ))}
            </ul>
            <ol className='worksubtit'>
              <li className='subfirst'>
                <h4>Contents </h4>
                <h4>+ Commerce</h4>
              </li>
              <li className='subsecond'>
                <h4>Beauty </h4>
                <h4> Marketer</h4>
              </li>
              <li className='subthird'>
                <h4>Beauty Quality </h4>
                <h4>Control Expert </h4>
              </li>
            </ol>

            {/*JS/jquery !!   */}
            {/* {jqFn()} */}
          </div>
        </section>
      </div>

      <WorkSwipe mm='on' />
    </>
  );
}; ///////Company///////////

export default Work;
