function Mainpartners() {
  return (
    <>
      <div id='sixth' className='slide slide-six'>
        <div className="sixdisplay">
        <section className='inbx'>
          <div className='ourwork'>
            <article className='partnertitle'>
              <div className='partname'>
                <h3 className='h3tit'>Our Partners</h3>
                <h2 className='subtit'>WE WORKED</h2>
                <div className='atitaction'>
                  <div className='btitaction'> </div>
                  <h2 className='title'>FOR THEM</h2>
                </div>
              </div>
            </article>
            <article className='partnerexplain'>
              <div className='en first'>Bstudio Company Is Working With </div>
              <div className='en second'>Various Companies And Brands.</div>
            </article>
          </div>
        </section>
        <section className='partnerslider'>
          <article className='partnertrack'>
            <ul className='partnerslide'>
              {partner_data_first.map((x, i) => (
                <li key={i}>
                  <img src={x.src} />
                </li>
              ))}
              {/* {partner_data_first.map((x, i) => (
                <li>
                  <img src={x.src} />
                </li>
              ))} */}
            </ul>
            <ul className='partnerslide'>
              {partner_data_second.map((x, i) => (
                <li key={i}>
                  <img src={x.src} />
                </li>
              ))}
              {/* {partner_data_second.map((x, i) => (
                <li>
                  <img src={x.src} />
                </li>
              ))} */}
            </ul>
            <ul className='partnerslide'>
              {partner_data_third.map((x, i) => (
                <li key={i}>
                  <img src={x.src} />
                </li>
              ))}
              {/* {partner_data_third.map((x, i) => (
                <li>
                  <img src={x.src} />
                </li>
              ))} */}
            </ul>

            {/* <img src='../../../public/image/main_partner_0.png'></img> 
               */}
          </article>
        </section>
        </div>
      </div>
      {/*   */}
      {/* {loadFn()} */}
    </>
  );
}

export default Mainpartners;
