export default function App() {
  function loadFn() {
    window.onload = function () {
      // 
      let mob = 0;
      chkWid();
      function chkWid() {
        if (window.innerWidth <= 800) mob = 1;
        else mob = 0;
        console.log(':', mob);
      } ///// chkWid /////////////

      window.addEventListener('resize', chkWid);

      //  ;
      // 
      // 
      const ham = document.querySelector('.ham');
      ham.onclick = chgMenu;

      function chgMenu() {
        //   !
        if (!mob) return;
        let tg = document.querySelector('.top');
        tg.classList.toggle('on');
        let isGnb = tg.classList.contains('on');
        console.log('isGnb:', isGnb);
        if (isGnb) document.body.style.overflow = 'hidden';
        else document.body.style.overflow = 'visible';
      }

      /*   on   */
      const gnba = document.querySelectorAll('.gnb a');
      gnba.forEach((ele) => {
        ele.onclick = () => {
          ham.click();
        }; ///////////click/////////////
      }); /////forEach/////////////


      window.addEventListener('scroll', () => {
        // if (!document.querySelector('#main')) return; 

        if (!document.querySelector('.cont')) return;

        //      
        const mark = document.querySelectorAll('.mark');
        console.log('mark', mark);
        /* letterbx */
        const scTop = window.scrollY;
        console.log('scTop', scTop);
        //  
        const winH = window.innerHeight;
        console.log('winH', winH);

        const main = document.querySelector('#main');
        const mainheight = main.clientHeight;

        // 

        const scLimit = mainheight - winH;
        console.log(':', scLimit);

        //   
        const wtop = (winH * scTop) / scLimit;
        console.log('w:', wtop);

        console.log('w:');

        const gps = (ele) => ele.getBoundingClientRect().top;
        // console.log(gps())

        //    on    
        // const showIt = (x)=> {
        //   let xval = gps(x);
        //   if(xval<hv && xval>0){
        //     x.classList.add('on')
        //   }
        // }/////////showIt///////
        console.log('', scTop);

        mark.forEach((v, i) => {
          // let val = gps(v);
          // console.log('mark', gps(v));

          // 
          if (i == 0) {
            if (scTop > 0) {
              mark[i].classList.add('on');

              mark[i].classList.add('color' + i);
              console.log('11~!!!', mark[i], i);
            } ////////if//////////
          } ///////mark.forEach((v, i)=>{ /foreach//////
          else if (i == 1 || i == 2) {
            if (scTop > i * 80 + 600) {
              mark[i].classList.add('on');

              mark[i].classList.add('color' + i);
              console.log('21~!!!');
            } ////////if//////////
          }
        }); ///////mark.forEach((v, i)=>{ /foreach//////

        //  const logoh5= document.querySelectorAll(logoletter h5)

        ////////Logoletter.js////////
        const logoh5 = document.querySelectorAll('h5.logo');

        //console.log('logoh5', logoh5);

        logoh5.forEach((v, i) => {
          // let val = gps(v);
          let val = scTop;

          const percentage = 1 - Math.max(val / mainheight);

          const pos = percentage * 2000;
          // console.log(pos);

          v.style.backgroundPosition = `-${pos}vw -${pos}vw`;
        }); ///////////  logoh5.forEach((v,i)=>{//////////

        const atit = document.querySelectorAll('.atitaction h2.title');

        console.log('!!!atit', atit);
        const btit = document.querySelectorAll('.btitaction');
        // console.log('btit', btit);

        //  const textexplain = document.querySelector('.textexplain');
        //  console.log('textexplain', gps(textexplain));

        const ultext = document.querySelectorAll('ul.text');

        const second = document.querySelector('.partnerexplain .second');

        const partnerslide = document.querySelectorAll('.partnerslide');

        if (wtop > winH * 0.6 && atit[0]) {
          // if (wtop > 600 && atit[0]) {
          atit[0].classList.add('on');
          btit[0].classList.add('on');

          setTimeout(() => {
            ultext.forEach((v) => {
              v.classList.remove('hidden');
            });
          }, 120);

          // console.log('gps(btit)', gps(btit));
        } ////////if///////
        // else if (wtop <= 600 && atit[0]) {
        else if (wtop <= winH * 0.6 && atit[0]) {
          atit[0].classList.remove('on');
          btit[0].classList.remove('on');

          ultext.forEach((v) => {
            v.classList.add('hidden');
          });
          // console.log("12~!!!");
        }
        // else if (wtop > 680) {
       

        if (wtop > winH * 0.6 && atit[1]) {
          // if (wtop > wtop * 0.55 && atit[1]) {
          atit[1].classList.add('on');
          btit[1].classList.add('on');
          second.classList.add('on');
          partnerslide.forEach((v, i) => {
            v.classList.add('on');
          });
        }

        // else if (wtop <= wtop * 0.55 && atit[1]) {
        else if (wtop <= winH * 0.6 && atit[1]) {
          atit[1].classList.remove('on');
          btit[1].classList.remove('on');
          second.classList.remove('on');
          // console.log("22~!!!");
        }

        ////////////////////

        if (wtop > 900) {
          // atit[2].classList.add('on');
          // btit[2].classList.add('on');
          // second.classList.add('on');
          // partnerslide.forEach((v, i) => {
          //   v.classList.add('on');
          // });
        } else if (wtop <= 900) {
          // atit[2].classList.remove('on');
          // btit[2].classList.remove('on');
          // second.classList.remove('on');
        }
      }); ///////scroll////
    }; //////window.onload = function(){///////////
  } ///////loadFn////

  function jqFn() {
    //  :   
    $(() => {
      $(window).scroll(function () {
        if (!document.querySelector('.slide')) return;
        let windowHeight = $(this).height();
        let scrollDistance = $(this).scrollTop();
        let slides = $('.slide');
        let slideCount = slides.length;
        console.log('slideCount', slideCount);
        console.log('1scrollDistance', scrollDistance);
        console.log('1windowHeight', windowHeight);

        $('.cont').css('height', slideCount * 100 + 'vh');
        // 
        // $('#main').css('height', slideCount * 100 + 'vh'); 

        function slideReset() {
          $(slides).each(function () {
            $(this).css('height', $(window).height() + '');
          });
        } //////////slideReset()////////////////

        if (scrollDistance <= windowHeight) {
          let maxHeight = windowHeight - scrollDistance;
          slideReset();
          $('.slide-one').css('height', maxHeight);
        } //////if(scrollDistance <= windowHeight)////////
        else if (scrollDistance <= windowHeight * 2) {
          let maxHeight = windowHeight * 2 - scrollDistance;
          slideReset();
          $('.slide-two').css('height', maxHeight);
          $('.slide-one').css('height', 0);
        } else if (scrollDistance <= windowHeight * 3) {
          let maxHeight = windowHeight * 3 - scrollDistance;
          slideReset();

          $('.slide-three').css('height', maxHeight);

          $('.slide-two').css('height', 0);
          $('.slide-one').css('height', 0);
        } else if (scrollDistance <= windowHeight * 4) {
          let maxHeight = windowHeight * 4 - scrollDistance;
          slideReset();
          $('.slide-four').css('height', maxHeight);
          $('.slide-three').css('height', 0);
          $('.slide-two').css('height', 0);
          $('.slide-one').css('height', 0);
        } else if (scrollDistance <= windowHeight * 5) {
          let maxHeight = windowHeight * 5 - scrollDistance;
          $('.slide-five').css('height', maxHeight);
          $('.slide-four').css('height', 0);
          $('.slide-three').css('height', 0);
          $('.slide-two').css('height', 0);
          $('.slide-one').css('height', 0);
        } else if (scrollDistance <= windowHeight * 6) {
          let maxHeight = windowHeight * 6 - scrollDistance;
          $('.slide-six').css('height', maxHeight);
          $('.slide-five').css('height', 0);
          $('.slide-four').css('height', 0);
          $('.slide-three').css('height', 0);
          $('.slide-two').css('height', 0);
          $('.slide-one').css('height', 0);
        } else if (scrollDistance <= windowHeight * 7) {
          let maxHeight = windowHeight * 7 - scrollDistance;
          $('.slide-seven').css('height', maxHeight);
          $('.slide-six').css('height', 0);
          $('.slide-five').css('height', 0);
          $('.slide-four').css('height', 0);
          $('.slide-three').css('height', 0);
          $('.slide-two').css('height', 0);
          $('.slide-one').css('height', 0);
        }

        console.log('!!');

        //
        // console.log("scrollDistance",scrollDistance);
      });
    });
  } ///////jqFn/////
  return (
    <HashRouter>
      <Routes>
        <Route path='/' element={<Layout />}>
          <Route index element={<Main />} />
          <Route path='main' element={<Main />} />
          <Route path='company' element={<Company />} />
          <Route path='service' element={<Service />} />
          <Route path='work' element={<Work />} />
        </Route>
      </Routes>
      {loadFn()}
      {jqFn()}
    </HashRouter>
  );
}

const root = ReactDOM.createRoot(document.getElementById('root'));
root.render(<App />);
