import React from 'react';

import isrc from './Imgsrc';

const Logo = (props) => {
  const logostyle = {
    top:{
    width: '40px',
    height: '40px',
  }
  };

 


  return (
    <h1 style={logostyle[props.gb]}>
      <img src={isrc.logo} />
    </h1>
  );
}; /////Logo//////////

export default Logo;
