// Temp.js

import $ from 'jquery';
import '../css/firstletter.css';
import firstletter_data from '../data/firstletter';

//   

/* 

rgb(103, 65, 242)

*/

function Firstletter() {

  const sdt = firstletter_data;


  const img = {
    arrow: './image/svg/arrow.svg',
    src: './image/svg/influencer.svg',
    phone: './image/main_01.png',
    talk: './image/main_02.png',
  };

  return (
    <div id='first' className='slide slide-one'>
      <section className='firstletterbx inbx'>
        <article className='firstletter'>
          <h2>
            <span>{sdt.tit.split('^')[0]} </span>

            <span className='underline'>{sdt.tit.split('^')[1]}</span>
            <span className='underlineafter'></span>
            <span className='commerce'> {sdt.tit.split('^')[2]}</span>
          </h2>
          <h2>
            <span>{sdt.tit.split('^')[3]}</span>

            <span className='arrow'>
              <img src={img.arrow} alt={'arrow'} />
              {/* <img src='https://seo-001.github.io/react_portfolio_3/image/svg/arrow.svg' alt='arrow' /> */}

              {/* https://seo-001.github.io/react_portfolio_3 */}
              {/* "https://.github.io//img/image.jpg */}
              {/* <img src={process.env.PUBLIC_URL + '/image/svg/arrow.svg'} alt={'arrow'} /> */}
              {/* <img src={require('./image/svg/arrow.svg').default} alt={'arrow'} /> */}
              {/* {require('./src_assets/logo192.png').default} */}
              {/* {process.env.PUBLIC_URL + '/img/imgTest.jpg'} */}
            </span>

            <span className='double'>{sdt.tit.split('^')[4]}</span>
            <span className='doubleafter'>{sdt.tit.split('^')[4]}</span>
          </h2>
          <div className='influencer'>
            <img src={sdt.src} alt='influencer' />
          </div>
          <h2>
            <span className='mark'>{sdt.tit.split('^')[5]}</span>
          </h2>

          <h4 className='firstletter'>
            <span>{sdt.exp.split('^')[0]}</span>
            <span className='focus'>{sdt.exp.split('^')[1]}</span>
            <span>{sdt.exp.split('^')[2]}</span>
          </h4>
        </article>
      </section>
    </div>
  );
}

export default Firstletter;
