//    Ban.js

//  CSS
import './css/ban.css';
//  
import ban_data from './data/banner';

import $ from 'jquery';

// !! ///
function jqFn() {
  $(() => {
    ///jQB

    //   
    let prot = 0;

    // 1.    
    $('.abtn').click(function () {
      // 0. 
      if (prot) return;
      prot = 1;
      setTimeout(() => (prot = 0), 400);

      // 1.  
      let isB = $(this).is('.rb');
      console.log('?', isB);

      //   
      const tg = $(this).siblings('.slider');

      // 2.  
      // (1)   
      if (isB) {
        tg.animate({ left: '-100%' }, 400, function () {
          // 1 li  !!
          $(this).append($(this).find('li').first()).css({ left: '0' });
        }); /////////animate////////
      } /////////if////////

      // (2)   :  
      else {
        tg.prepend(tg.find('li').last()).css({ left: '-100%' }).animate({ left: '0' }, 400);
      } /////////else///////

      // 3.     "on"
      // : .indic li

      $('.indic li').eq(tg.find('li').eq(isB).attr('data-seq')).addClass('on').siblings().removeClass('on');
    }); ///////click//////////
  }); ////////////////jQB////////////////
}

//    
function MakeList(props) {
  return (
    <li data-seq={props.idx}>
      <div>
      <img className='banimg' src={props.rec['src']} alt='' />
      </div>
      <h3 className="tithead">{props.tit['tit1']} </h3>
      
      <h3 className="tithead">{props.tit['tit2']} </h3>
     
      <h3 className="tithead">{props.tit['tit3']} </h3>

    </li>
  );
} ///////MakeList////////////////

//  

/*     <section className={'vidbox'+' '+props.mm}>  */

function Ban(props)
{
  const sel_data = ban_data[props.cat];

  return (
    <div id='fourth' className={'slide slide-four' +' '+props.mm} >
      <section className='inbx headline'>
        <h2>{props.cat}</h2>
        <div className='banner'>
          {/*  */}
          <ul className='slider'>
            {sel_data.map((x, i) => (
              <MakeList rec={x} tit={x} key={i} idx={i} />
            ))
            }
          </ul>
         
          {
            /* 1   */

            sel_data.length > 1 && (
              <>
                {/*  */}
                <button className='abtn lb'>  </button>
                <button className='abtn rb'>  </button>

                {/*   */}
                <ol className='indic'>
                  {sel_data.map((x, i) => (
                    <li className={i == 0 ? 'on' : ''} key={i}></li>
                  ))}
                </ol>
              </>
            )
          }
          
          {/*JS/jquery !!   */}
          {jqFn()}
        </div>
      </section>
    </div>
  );
} ///////Ban ///////////

export default Ban;
