import React, { useRef, useState } from 'react';
// Import Swiper React components
import { Swiper, SwiperSlide } from 'swiper/react';

/*   */
import $ from 'jquery';

// Import Swiper styles
import 'swiper/css';
import 'swiper/css/pagination';
import 'swiper/css/navigation';

import './swiperwork.css';

//  
import swiperwork_data from '../data/swiperwork';

// import required modules
import { Autoplay, Pagination, Navigation } from 'swiper';

export default function SwiperWork() {
  //  
  const sdt = swiperwork_data;

  return (
    <>
      <Swiper
        spaceBetween={30}
        loop={true}
        centeredSlides={true}
        autoplay={{
          delay: 1000,
          disableOnInteraction: false,
        }}
        pagination={{
          clickable: true,
        }}
        navigation={true}
        modules={[Autoplay, Pagination, Navigation]}
        className='mySwiper'
      >
        {sdt.map((v, i) => (
          <SwiperSlide key={i}>
            <article className='insta'>
              <div className='firstline'>
                <div className='isrc'>
                  <img className='instaimg' src={v.isrc} alt={v.name} />
                </div>
                <p className='instaname'>{v.insta}</p>
              </div>
              <div className='msrc'>
                <img className='swiperimg' src={v.msrc} alt={v.name} />
                <p className='msrctit'>{v.tit}</p>
              </div>
            </article>
          </SwiperSlide>
        ))}
      </Swiper>
    </>
  ); ////////return////
} //////// function SwiperWork()//////
