import React from 'react';
import Ban from './Ban';
import Firstletter from './main/Firstletter';
import Mainletter from './main/Mainletter';
import Logoletter from '../3rdpj/module/Logoletter';
import WorkSwipe from './module/WorkSwipe';
import Mainpartners from './main/Mainpartners';

import News from './main/News';


const Main = () => {
  return (
    <>
      <Firstletter />
      <Mainletter />
      <Logoletter cat='MAIN' />
      <Ban cat='MAIN' />
      <WorkSwipe />
      <Mainpartners />
      <News />
    </>
  );
};

export default Main;
